@import 'vars';
@import 'mixins';
// @import 'popups/add-comment-popup';
// @import 'parts/drug-and-drop';
@import 'blocks/float-block';
@import 'blocks/similar-products';

.page-title {
    margin: 35px 0 35px 0;
}
.tabs__controls {
    border-top: 1px solid #f0eceb;
}
.open-product-main {
    @include clearfix;
    position: relative;
    background: #fff;
    .left-block {
        float: left;
        width: 540px;
    }
    .right-block {
        float: right;
        width: 660px;
        padding-top: 18px;
        
    }
    .own-set__wrap-items {
        margin-top: 0;
        margin-left: 25px;
    }
    .prod-options .open-prod-controls--border {
        position: relative;
        &::after {
            position: absolute;
            display: block;
            content: '';
            width: 645px;
            // width: calc(97% + 3px);
            height: 1px;
            background-color: #eee;
            bottom: 0;
            right: 0;
        }
    }
    .prod-options .add-comment-btn-wrap {
        padding-bottom: 0;
    }
    .open-prod-controls__col--inline {
        align-self: center;
        .open-prod-controls__to-cart-btn {
            margin-top: 0;
            margin-right: 20px;
        }
        .buy-in-click-btn {
            margin: 0;
            max-width: 80px;
            text-align: center;
            vertical-align: middle;
        }
    }
    .prod-options__date-block .prod-options__date {
        line-height: 16px;
    }
}


.float-block {
    .open-prod-controls__col--price {
        margin-left: 25px;
    }
    .open-prod-controls__price-block {
        line-height: normal;
        padding-top: 0;
    }
}