.similar-products {
    height: 400px;
    position: relative;
    background-color: #fff;
    padding: 0 40px;
    .similar-products__swiper {
        height: 100%;
        padding: 50px 10px;
        .product-block {
            width: 275px;
        }
    }
    &__wrapper {
        z-index: 0;
        .swiper-slide {
            width: auto;
        }
    }
    .product-block__inner .product-block__img-block {
        max-width: 270px;
        width: auto;
    }
    .price-block__title--new-line {
        display: block;
        padding-left: 15px;
    }
}

.product-block__btns-block {
    .add-to-cart-btn, .add-to-wishlist-btn {
        float: none !important;
    }
    .add-to-wishlist-btn {
        margin-left: 6px;
        margin-top: 15px;
    }
}
