.float-block-wrap {
    // margin-top: 100px;
    position: relative;
    background: #ffffff;
}


.float-block {
    width: 300px;
    border-radius: 3px;
    border: 1px solid #ececec;
    background-color: #ffffff;
    position: absolute;
    top: 36px;
    //top: 160px; //initial position
    right: 15px;
    
    &__header {
        padding: 15px 12px 15px 12px;
        padding-bottom: 15px;
        font-size: 14px;
        font-weight: 700;
        color: #2b2b2b;
        background-color: #f3f3f3;
    }
    &__content {
        padding: 15px 12px 15px 12px;
    }
    &__pic {
        width: 150px;
        height: 120px;
        margin: 0 auto;
        margin-bottom: 15px;
        img {
            display: block;
            max-width: 100%;
            height: auto;
            object-fit: cover;
        }
    }

    .prod-options {
        &__form {
            margin-top: 0;
            margin-bottom: 13px;
        }
        &__select-wrap {
            display: flex;
            justify-content: space-between;
            align-items: center;
            &:not(:last-child) {
                margin-bottom: 5px;
            }
        }
        &__select-wrap:not(:first-child) {
            margin-left: 0;
        }
        &__title {
            margin-right: 8px;
        }
        &__select {
            width: 204px;
        }
    }

    .open-prod-controls {
        margin-bottom: 10px;
    }

    .open-prod-controls__to-cart-btn {
        width: 100%;
        height: 42px;
        line-height: 42px;
        margin-bottom: 13px;
    }
    .add-to-wishlist-btn {
        display: block;
        margin: 0 auto;
        margin-bottom: 15px;
        &__title {
            font-size: 14px;
        }
    }
    .buy-in-click-btn {
        display: block;
        text-align: center;
    }
}

.fixed-float-block {
    position: fixed;
    right: auto;
    margin-top: 0;
    top: 100px !important;
}

.fixed-bottom-float-block {
    position: absolute;
    top: auto !important;
    bottom: 20px;
}
